@import url(https://fonts.googleapis.com/css?family=Russo+One);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab);
body {
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;

  background: black;
}

li {
  list-style: none;
}

p {
  margin: 5px;
  padding: 0;
}

h2.Section_funny-title__1I8aY {
  border-top: 5px solid #e0dbde;
  border-bottom: 5px solid #e0dbde;
  font-size: 70px;
  text-align: center;
  margin-top: 40px;
  margin-bottom: 5px;
  text-transform: uppercase;
  font-family: 'Russo One', sans-serif;
  font-weight: 900;
  letter-spacing: 8px;
}
@keyframes Section_move-background__2K8TP {
  0% {
    background-position: -300px 0;
  }
  100% {
    background-position: 300px -300px;
  }
}
.Section_title__2zYNS {
  text-align: center;
  animation-play-state: running;
  color: transparent;
  background: url(https://obninsksite.ru/assets/theme/images/blog/square.svg)
    no-repeat #ece7ea;
  background-size: cover;
  -webkit-text-fill-color: transparent;
  -moz-text-fill-color: transparent;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text;
  animation: Section_move-background__2K8TP 20s infinite linear alternate;
}

.FeedbackOptions_wraper__19PHT {
  text-align: center;
}

.Button_button__3ydOD {
  color: #232323;
  background: transparent;
  border: 1px solid #232323;
  position: relative;
  font-size: 14px;
  letter-spacing: 0.3em;
  font-family: 'Montserrat', sans-serif;
  padding: 17px 34px 17px 39px;
  transition: 0.2s ease-in-out;
  cursor: pointer;
  margin-right: 20px;
}
.Button_button__3ydOD:before {
  content: '';
  position: absolute;
  top: 5px;
  left: 5px;
  width: 100%;
  height: 100%;
  background: #32d4e2;
  z-index: -1;
  transition: 0.25s ease;
  transform: translate(0, 0);
}
.Button_button__3ydOD:hover:before {
  transform: translate(4px, 4px);
}

.Statistics_statistics__2tYwc {
  font-size: 25px;
  color: aliceblue;
}

span {
  font-size: 25px;
  color: aqua;
}

.Notification_notification__12DzH {
  font-family: 'Roboto Slab', serif;
  text-align: center;
  font-size: 50px;
  font-weight: normal;
  color: transparent;
  text-shadow: 0 0 1px #eee, 5px 5px 1px #00dcdc, -5px -2px 1px #ff3232;
  animation: Notification_glitch__19jP6 4s ease-in-out infinite alternate;
}
@keyframes Notification_glitch__19jP6 {
  0%,
  40%,
  44%,
  58%,
  61%,
  65%,
  69%,
  73%,
  100% {
    transform: skewX(0deg);
  }
  41% {
    transform: skewX(10deg);
  }
  42% {
    transform: skewX(-10deg);
  }
  59% {
    transform: skewX(40deg) skewY(10deg);
  }
  60% {
    transform: skewX(-40deg) skewY(-10deg);
  }
  63% {
    transform: skewX(10deg) skewY(-5deg);
  }
  70% {
    transform: skewX(-50deg) skewY(-20deg);
  }
  71% {
    transform: skewX(10deg) skewY(-10deg);
  }
}

